import React, { Component } from 'react'
import { Auth } from 'aws-amplify'

// import UserContext from './UserContext'
// import { withRouter } from 'react-router-dom'

import LoaderButton from "../../components/LoaderButton";
import { FaFacebook, FaGoogle, FaEnvelope } from 'react-icons/fa'
import Snackbar from '@material-ui/core/Snackbar';

import SignInWithFacebook from '../../components/Auth/FacebookSignIn';
import SignInWithGoogle from '../../components/Auth/GoogleSignin';

import { signIn, signOut, checkUser } from '../../actions/authActions';

import { connect } from 'react-redux';
import PropTypes from 'prop-types';

class SignIn extends Component {
    constructor(props) {
        super(props);

        this.state = {
            username: '',
            password: '',
            showConfirmation: false,
            user: {},
            authCode: '',
            errorMessage:'',
            showSnack: false
        }

        // this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount() {
        const { history } = this.props;
    }

    onChange = (key, value) => {
        this.setState({
            [key]: value
        })
    }

    handleOnSignin = (e) => {
        e.preventDefault();

        const data = this.state;

        console.log('Handle Signin', data);

        // this.validateForm(data);

        this.props.signIn(data)
            .then( res => {
                //redirect to browse
                this.props.history.push('/browse')
            } )
            .catch ( err => {
                console.log( err );

                // set message = Err message
            });
    }

    /* confirmSignIn = () => {
        Auth.confirmSignIn(this.state.user, this.state.authCode, this.state.user.challengeName)
            .then(user => {
                this.history.push('/')
            })
            .catch(err => console.log('error confirming signing in...: ', err))
    } */

    redirectForgetPass = () => {
        this.props.history.push('/forget-password');
    }

    validateForm = () => {
        // check if email is valid format
        // check not empty
        return (
            this.state.username.length > 0 &&
            this.state.password.length > 0 
        );

        // on error add error class to input
    }

    handleSnackbarClose = () => {
        this.setState({
            showSnack: false,
            message: '',
        });
    };  

    render() {
        const { message, showSnack } = this.props;
        
        return (
            <div>
                { message !=='' ? (
                    <Snackbar
                        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                        message = {message}
                        autoHideDuration = {3000}
                        onClose = {this.handleSnackbarClose}
                        // open = {showSnack}
                        ContentProps = {styles.root}
                    />
                    ) : null
                }
        
                <div className="signup-form">
                    <h2 className="title"> Access Your Free Modshow Internet TV Channel </h2>

                    <SignInWithFacebook {...this.props} />

                    <SignInWithGoogle {...this.props} />
            
                    <h4 className="break"><span>Or</span></h4>
                    { !this.state.showConfirmation && (
                        <form onSubmit={this.handleOnSignin}>
                            <div className="formContainer">
                                <input
                                    onChange={evt => this.onChange('username', evt.target.value)}
                                    style={styles.input}
                                    placeholder='username' />
                                
                                <input
                                    type='password'
                                    onChange={evt => this.onChange('password', evt.target.value)}
                                    style={styles.input}
                                    placeholder='password' />
                                
                                <LoaderButton
                                    block
                                    type="submit"
                                    bsSize="large"
                                    loadingText="Signing in...."
                                    text="Sign In"
                                    disabled={!this.validateForm()} />
                                
                                <p className="">
                                    <a
                                        className="linkText"
                                        onClick={this.redirectForgetPass} >
                                        Recover Password?
                                    </a>
                                </p>
                            </div>
                        </form>
                    )}
                </div>
            </div>
        )
    }
}

SignIn.propTypes = {
    signIn: PropTypes.func.isRequired,
    signOut: PropTypes.func.isRequired,
    checkUser: PropTypes.func.isRequired
}
  
const mapStateToProps = state => ({
    //show: state.shows.item.data.getShow,
    auth: state.users
});

export default connect(mapStateToProps, { signIn, signOut, checkUser })(SignIn);

const styles = {
    root: {
      background: 'red'
    },
    container: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center'
    },
    footer: {
      fontWeight: '600',
      padding: '0px 25px',
      textAlign: 'center',
      color: 'rgba(0, 0, 0, 0.6)'
    },
    anchor: {
      color: '#006bfc',
      cursor: 'pointer'
    },
    icon: {
      display:'inline',
      color: 'red'
    }
  }