import { CHECK_USER, SIGNIN_SUCCESS, SIGNIN_FAIL, SIGNOUT } from './types';
import { Auth } from 'aws-amplify';

// import { getShow, getSeason } from '../graphql/queries';
// import { updateRatingShow } from '../graphql/mutations';
// import awsmobile from '../aws-exports.js';

// API.configure(awsmobile);

// Check if user is logged in or not
export const checkUser = () => async dispatch => {

    Auth.currentAuthenticatedUser()
        .then(
            user => {
                dispatch({
                    type: CHECK_USER,
                    payload: user,
                })
                // console.log('Check User Ok', user)
            }
        )
        .catch(
            err => {
                dispatch({
                    type: CHECK_USER,
                    payload: null,
                })
            }
        );
}

// Auth Signin Cognito
export const signIn = data => async dispatch => {
    Auth.signIn(data.username, data.password)
        .then(
            user => {
                dispatch({
                    type: SIGNIN_SUCCESS,
                    payload: user,
                    message: "Ok"
                })
            }
        )
        .catch(
            err => {
                dispatch({
                    type: SIGNIN_FAIL,
                    payload: err,
                    message: err.message
                })

                // console.log('Sign In Fail', err)
            }
        );
}

// Auth Signout Cognito - User Pools and Federated
export const signOut = () => async dispatch => {
    Auth.signOut()
        .then(
            () => {
                dispatch({
                    type: SIGNOUT,
                    payload: ''
                })
            }
        )
        .catch(
            err => {
                console.log('Signout Fail', err);
            }
        );
}

