import React, { Component } from 'react'

import { Redirect } from 'react-router-dom'

import Signin from './Signin'; 
import Signup from './Signup';

import logo from '../../assets/img/modshow-logo.png';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { checkUser}  from '../../actions/authActions';

// import { switchSign } from './actions/authActions';


class Authenticator extends Component {
    constructor(props) {
        super(props);

        this.state = {
            switchSign: true
        }

        // this.handleSwitchSign = this.handleSwitchSign.bind(this);
    }

    componentDidMount(){
        this.props.checkUser();
    }

    handleSwitchSign = bool => {
        
        this.setState({
            switchSign: bool
        });
    }

    render() {
        // console.log(this.state);

        const { switchSign } = this.state;

        console.log('isAuthenticated', this.props.isAuthenticated);

        return (
            (!this.props.isAuthenticated) ? (
            <div style={styles.container} className="authContainer">
                <div className="authLogo"><img src={logo}/></div>
                
                { switchSign ? (
                    <Signin {...this.props} />
                ) : (
                    <Signup {...this.props} />
                )}

                <div className="white linkText">
                    { switchSign ? (
                        <p onClick={() => this.handleSwitchSign(false)}>
                            Need an account? Sign Up
                        </p>
                    ) : (
                        <p onClick={() => this.handleSwitchSign(true)}>
                            Already have an account? Sign In
                        </p>
                    )}
                </div>
            </div>
            ) : (
                <Redirect to="/browse" />
            )
        )
    }
}

Authenticator.propTypes = {
    auth: PropTypes.object.isRequired,
    isAuthenticated: PropTypes.bool.isRequired,  
    // loading: PropTypes.bool.isRequired
    checkUser: PropTypes.func.isRequired
}
  
const mapStateToProps = state => ({
    auth: state.auth.item,
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { checkUser })(Authenticator);

// export default Authenticator; 

const styles = {
    container: {
        marginTop: 0
    },
    buttonContainer: {
        display: 'flex',
        justifyContent: 'center'
    },
    toggle: {
        paddingBottom: '10px',
        cursor: 'pointer',
        marginTop: 25,
        borderBottom: '2px solid transparent',
        
        ':hover': {
            opacity: 0.6
        }
    }
}