import React from 'react'
import { withRouter } from 'react-router-dom'
import { FaFacebook, FaGoogle, FaEnvelope } from 'react-icons/fa'
import { Auth } from 'aws-amplify'
import SignInWithFacebook from '../../components/Auth/FacebookSignIn';
import SignInWithGoogle from '../../components/Auth/GoogleSignin';
// import UserContext from './UserContext'
import MessageBar from '../../components/MessageBar'

class SignUp extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            username: '',
            password: '',
            email: '',
            phone_number: '',
            authCode: '',
            showConfirmation: false,
            errorMessage: ''
          }
        this.confirmSignUp = this.confirmSignUp.bind(this);
        this.signUp = this.signUp.bind(this);
    }
  

    // static contextType = UserContext
    onChange = (key, value) => {
        this.setState({
            [key]: value
        })
    }

    signUp = () => {
        const { username, password, email, phone_number } = this.state

        Auth.signUp({
            username,
            password,
            attributes: {
                email,
                phone_number
            }
        })
        .then(() => this.setState({ showConfirmation: true }))
        .catch(err =>  {
            console.log('signup error ',err);
            switch (err.code) {
                case 'UsernameExistsException':
                    console.log('Username exist');
                    this.setState({ errorMessage: 'Username exist, please try again'});
                    break;
                
                case 'signIn_failure':
                    console.log('user is not signed in');
                    break;
          
                default:
                    break;
            }
        });
    }

    confirmSignUp = () => {
        Auth.confirmSignUp(this.state.username, this.state.authCode)
            .then(() => this.props.history.push('/browse'))
            .catch(err => console.log('error confirming signing up: ', err))
    }

    componentDidMount() {
        const { history } = this.props
        const { updateCurrentUser } = this.context
        
        Auth.currentAuthenticatedUser()
            .then(user => {
                if (!user.signInUserSession) {
                    // this.setState({ user, showConfirmation: true })
                    console.log(user);
                } else {
                    updateCurrentUser(user)
                    //console.log(user);
                    history.push('/browse');
                }        
            })
            .catch(err => console.log(err))
    }

    render() {
        const { showConfirmation, errorMessage } = this.state;
    
        return (
            <div>
                {errorMessage ? (
                    <MessageBar className="messageBar" type="error" message={errorMessage} />  
                ) 
                : ""
                }

                
                <div style={styles.container} className="signup-form">
                    {!showConfirmation && (

                    <div >
                        <h2 className="title"> Access Your Free Modshow Internet TV Channel </h2>
            
                        <SignInWithFacebook {...this.props} />

<SignInWithGoogle {...this.props} />

                        <h4 className="break"><span>Or</span></h4>
                        
                        <input
                            placeholder='Username'
                            style={styles.input}
                            onChange={evt => this.onChange('username', evt.target.value)} />
                
                        <input
                            placeholder='Password'
                            type='password'
                            style={styles.input}
                            onChange={evt => this.onChange('password', evt.target.value)} />
                
                        <input
                            placeholder='Email'
                            style={styles.input}
                            onChange={evt => this.onChange('email', evt.target.value)} />

                        <input
                            placeholder='Phone Number'
                            style={styles.input}
                            onChange={evt => this.onChange('phone_number', evt.target.value)} />
                
                        <button onClick={this.signUp} style={styles.button}>
                            Sign Up
                        </button>
                    </div>
                )}
                
                {showConfirmation && (
                    <div >
                        <input
                            onChange={evt => this.onChange('authCode', evt.target.value)}
                            placeholder='Confirmation Code' />
              
                        <button onClick={this.confirmSignUp} style={styles.button}>
                        Confirm Sign Up
                        </button>
                    </div>
                )}
            </div>
      </div>
    )
  }
}
export default SignUp;

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center'
  },
  footer: {
    fontWeight: '600',
    padding: '0px 25px',
    textAlign: 'center',
    color: 'rgba(0, 0, 0, 0.6)'
  },
  anchor: {
    color: '#006bfc',
    cursor: 'pointer'
  },
  icon: {
    display:'inline',
    color: 'red'
  }
}



